.App {
  text-align: center;
}
body {
  font-family: 'nanumsquare';
  background-color: beige;
}

header {
  background-color: pink;
}
h1 {
  color: #fff;
  text-align : left;
  padding-left : 20px;
  padding-top : 5px;
  padding-bottom : 5px;
  font-weight: 700;
  font-size: 30px;
}


.bigContainer {
  position: relative;
  display:flex;
  justify-content: space-between;
  flex-wrap: wrap;
  gap : 10px;
}


/* list of contents */
.listContainer {
  background-color: rgba(255,255,255,0.8);
  margin: 5px auto 20px;
  border-radius: 10px;
  padding-bottom:10px;
  min-width: 480px;
  width : 60%
}
.list {
  text-align: left;
  margin : 15px auto 0;
  padding-left: 20px;
  padding-right: 20px;
  
}
.list h3 {
  color: rgb(252, 166, 180);
  font-size: 20px;
  padding-bottom: 10px;
}
.list p {
  color: pink;
  font-size: 17px;
  padding-bottom : 10px;
  border-bottom: 1px solid pink;
}

.btnChange {
  color : #fff;
  background-color: pink;
  border : transparent;
  padding: 3px 9px;
  font-weight: 500;
  font-size: 15px;
  border-radius: 3px;
  cursor: pointer;
}
.btnChange:hover, .btnChange.focus {
  background-color: rgb(252, 166, 180);
}

.indexModal, .selectedModal {
  word-break: break-all;
  background-color: rgb(252, 169, 183);
  color: #fff;
  width: 80%;
  margin : 20px auto;
  padding: 20px 15px;
  border-radius: 10px;
}
.indexModal:hover, .selectedModal:hover {
  background-color: rgb(243, 150, 166);
}
.indexModal h2, .selectedModal h2 {
  padding-bottom : 20px;
}
.indexModal p, .selectedModal p {
  padding-bottom : 10px;
}
.showModal {
  cursor : pointer; 
}

.btnMore {
  margin-top : 20px;
  color : #fff;
  background-color: skyblue;
  border : transparent;
  padding: 6px 9px;
  font-weight: 500;
  font-size: 15px;
  border-radius: 3px;
  cursor: pointer;
}
.btnMore:hover {
  background-color: rgb(88, 192, 233);
}
.infoModal h2 { 
  color : rgb(152, 213, 236);
}
.infoModal a {
  color : #aaa;
}

.newContentContainer {
  position: relative;
  margin : 10px auto;
  width : 30%;
  max-width : 340px;
  min-width : 270px;
  display: flex;
  flex-direction: column;
}
.newTitle {
  width : 100%;
  display : flex;
  flex-direction: row;
  justify-content: space-between;
  white-space: nowrap;
  margin-bottom: 5px;
}
.newTitle label {
  display:inline-block;
  width: 15%;
  min-width : 50px;
  text-align: left;
  color : rgb(100, 100, 100);
  font-weight: 700;
}
.newTitle input {
  display: inline-block;
  width: 85%;
  border-radius: 5px;
  border : 1px solid rgb(100, 100, 100);
  padding : 2px 5px;
}
.newContent textarea {
  width : 100%;
  padding : 3px 6px;
  border-radius: 5px;
  border : 1px solid rgb(100, 100, 100);
  word-break: break-all;
}
.newContentBtn {
  display: flex;
  justify-content: right;
}
.newContentBtn button{
  color : #fff;
  background-color: rgb(100, 100, 100);
  border : transparent;
  padding: 3px 6px;
  font-weight: 500;
  font-size: 15px;
  border-radius: 3px;
  cursor: pointer;
}


.likeBtn {
  cursor : pointer;
}